table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  color: black;
}

table td,
table th {
  border: 1px solid #ddd;
  padding: 8px;
  color: black;
  /* border-top: none;
  border-bottom: none; */
  background-color: white;
}

table tr:nth-child(even) {
  background-color: #ffffff;
}

table tr:hover {
  background-color: #ddd;
}

table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: green;
  color: white;
}
